$text-color: #fff;

.content-wrapper {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
}

.naw-bar-wrapper {
  display: flex;
  flex-direction: column;
  padding: 10px;
  gap: 10px;
  background: #171717;
  max-width: 200px;
  width: 100%;

  .main-logo {
    fill: $text-color;
  }

  span {
    border-bottom: 1px solid #2f2f2f;
    padding: 20px 0;
  }

  .naw-bar-element {
    width: 100%;
  }

  .naw-bar-link {
    color: $text-color;
    padding: 10px 7px;
    border-radius: 10px;
    width: 90%;
    box-sizing: border-box;
    display: flex;
    transition: .3s;

    &:hover {
      background: #212121;
    }
  }

  .naw-bar-link.active {
    background: #2f2f2f;

    &:hover {
      background: #2f2f2f;
    }
  }
  
  .link-logo {
    display: flex;
    justify-content: center;
    transition: 1s;

    .main-logo {
      g {
        fill: #ccc;
      }
    }


    &:hover {
      g {
        fill: url(#SvgjsLinearGradient2141);
      }
    }
  }

  .active-link-logo {
    .main-logo {
      g {
        fill: url(#SvgjsLinearGradient2141);
      }
    }
  }
}